@import "@/utils/_variables.scss";

.pointers {
    display: flex;
    justify-content: center;
    flex-wrap: nowrap;
    &__cell {
        position: relative;
        width: 3.5rem;
        height: 3.5rem;
        svg {
            position: absolute;
            opacity: 0;
            transition: opacity 450ms linear;
            &.arrow {
                transform: rotate(180deg) translateY(-10px);
            }
            &.show {
                opacity: 1;
            }
        }
    }
}
